import React, { useEffect, useState } from 'react';
import { Avatar, Button, Container, TextField, Box, Typography, Input, InputLabel } from "@mui/material";
import { Lock } from "@mui/icons-material";
import axios from "axios";

function Join() {
    const [getpassword, setpassword] = useState("");
    const [getnickname, setNickname] = useState("");
    const [getNicknameResult, setNicknameResult] = useState("");
    //이메일인증 4개
    const [getemail, setEmail] = useState("");
    const [getEmailAuthentication, setEmailAuthentication] = useState("dsfaasdfsddsf9sdf9dsf9");
    const [getinputCode, setInputCode] = useState("");
    const [getcheckResult, setCheckResult] = useState("");

    //useState가 비동기식이라 한번씩 밀린다. 바로 동기화를 하기위해 이펙트를 사용
    useEffect(() => {
        console.log(getinputCode)
        getinputCode === getEmailAuthentication ? setCheckResult('이메일 인증번호가 일치합니다.') : setCheckResult('일치하지 않습니다.')
    }, [getinputCode]);


    const Hendlepass = (e) => {
        setpassword(e.target.value);
    }
    const HendleNick = (e) => {
        setNickname(e.target.value);
    }
    const HendleEmail = (e) => {
        setEmail(e.target.value);
    }
    const HandleEmailcange = (e) => {
        setInputCode(e.target.value);
    }
    //회원가입
    const handleCJoinlick = () => {
        if (getEmailAuthentication == getinputCode) {
            if (getNicknameResult == getnickname) {
                axios.post("register/signup", {
                    password: getpassword,
                    nickname: getnickname,
                    email: getemail,
                    createKey: getinputCode
                })
                    .then(
                        response => {
                            alert("회원가입에 성공했습니다");
                            window.location.replace("/");
                        }
                    )
                    .catch(Error => {

                        alert("회원가입에 실패했습니다. \n" + Error.response.data.message)
                    }
                    )
            }
            else {
                alert("중복된 닉네임 입니다. 다시 시도하세요.");
            }
        }
        else {
            alert("이메일 인증번호가 일치하지 않습니다.");
        }

    }

    const HandlenicknameCheck = () => {
        axios.get("/register/signup/" + getnickname)
            .then(
                response => {
                    setNicknameResult(response.data);
                    alert("사용가능한 닉네임 입니다.");
                }
            )
            .catch((Error) => {
                alert("중복된 닉네임 입니다. 다시 시도하세요.")
            }
            )
    }


    //이메일 인증 전송
    const HandleEmailAuthentication = () => {
        alert("인증번호가 발송되었습니다.");
        axios.get("/register/emailConfirm/" + getemail)
            .then(
                response => {
                    setEmailAuthentication(response.data);
                    console.log(getEmailAuthentication);
                }
            )
            .catch()

    }

    return (
        <Container component="main" maxWidth="xs">
            <Box sx={{ marginTop: 8, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                    <Lock />
                </Avatar>
                <Typography component='h1' variant='h5'>
                    회원가입
                </Typography>
                <TextField label='이메일' name='email' margin='normal' required fullWidth onInput={HendleEmail} />
                <Box>
                    <TextField label='인증번호' sx={{ margin: 2, marginTop: 3 }} name='EmailAuthentication' onChange={HandleEmailcange} />
                    <Button type='submit' variant='contained' sx={{ margin: 2, marginTop: 4 }} onClick={HandleEmailAuthentication}>이메일 인증</Button>
                    <InputLabel name='ResultColor' fullWidth style={{
                        marginLeft: 20,
                        color: getinputCode === getEmailAuthentication ? 'red' : '#3781E3'
                    }} >{getcheckResult}</InputLabel>
                </Box>

                <TextField label='비밀번호' name='password' margin='normal' type='password' required fullWidth onChange={Hendlepass} />
                <Box>
                    <TextField label='닉네임' name='nickname' margin='normal' required onChange={HendleNick} />
                    <Button type='submit' variant='contained' sx={{ margin: 2, marginTop: 3 }} onClick={HandlenicknameCheck}>중복 체크</Button>
                </Box>
                <Button type='submit' variant='contained' sx={{ marginTop: 2 }} onClick={handleCJoinlick}>회원가입</Button>
            </Box>
        </Container>
    );
}

export default Join;