import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setAccessToken, setNickname, setRefreshToken } from "../redux/actions";
import axiosInstance from "./setting/axiosInstance";

function Logout() {

    const dispatch = useDispatch();

    useEffect(() => {
        logoutapi()
    }, [])

    // 프론트엔드에서도 리프래쉬, 엑세스 토큰을 날려줘야 함.
    const logoutapi = () => {
        axiosInstance.post('/register/logout')
            .then((res) => {
                dispatch(setAccessToken(""))
                dispatch(setRefreshToken(""))
                dispatch(setNickname(""))
                alert("로그아웃 성공");
                window.location.replace('/');
            })
            .catch(error => {
                alert(error);
            })
    }

    return null; // 렌더링할 내용이 없으므로 null 반환
};




export default Logout;